body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

#table1, #table2 {
    display: none;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    margin-left: 2rem; 
    margin-right:2rem;
    align-self: center;
    margin-top: 5rem;
}

.content {
    margin: .4rem;
    color: black;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    display: flex;
    flex-direction: column;
}

.content p {
    font-size: .8rem;
}

.head {
    display: flex;
    flex-direction: row;
    vertical-align: baseline;
}

.head h2, .head p {
    margin-left: 1rem;
    margin-right: 1rem;
}

.AwaitingReview {
    color: red;
}

.Validated {
    color: lightgreen;
}

.Mediation {
    color: brown;
} 

.FormalConsequences {
    color: blueviolet;
}

.FinalInvestigativeReportSubmitted {
    color: yellow;
}

.Resolved {
    color: green;
}

.rows {
    background-image: url(/workSafeEmployee/static/media/bluepat2.1c05d40e.jpg);
    height: 8rem;
    width: 20rem;
    margin: 2rem;
    padding: 0px;
    border-radius: 25px;
    border: none;
    box-shadow: 0px 1px 1px 1px #00000062;
    
}

#table1 button, #table2 button {
    border: none;
    margin: 0px;
    padding: 0px;
    background-color: transparent;
}


h1 {
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    font-weight: 200;
    color: rgb(6, 99, 116);
    font-size: 4rem; 
    text-align: center;
}

.tabs {
    text-align: center;
    background-color: transparent;
}

.tab {
    margin-left: 3rem;
    margin-right: 3rem;
    border: none;
    background-color: transparent;
}

img {
    width: 3rem;
}
.form div {
    margin: 2rem;

}

.form {
    margin-top: 5rem;
}

h3 {
    font-size: 2rem;
    text-align: left;
}

.response p {
    color: rgb(6, 99, 116);
}
